<template>
  <div class="single-conversation"
    :id="'question' + conversation.code"
  >
    <system-bot
      :msg="conversation.question"
    ></system-bot>
    <div v-if="!answer">
      <p class="user-tips" v-if="conversation.optionList.length > 5">请从下列选项中选择一项</p>
      <p class="user-tips" v-else>请从以下{{conversation.optionList.length}}项选择一项</p>
    </div>
    <div class="user-response">
      <div class="chat-box"
        v-if="!answer"
      >
        <single-choice-select
          v-if="conversation.optionList.length > 5"
          v-model="answer"
          :option-list="conversation.optionList"
        ></single-choice-select>
        <single-choice-list
          v-else
          v-model="answer"
          :name="conversation.code"
          :option-list="conversation.optionList"
        ></single-choice-list>
      </div>
      <transition
        name="fade"
        mode="out-in"
      >
        <div class="selected-answer"
          v-if="answer"
        >
          {{ answer.text }}
        </div>
      </transition>
    </div>
  </div>
</template>
<script>
import SystemBot from './SystemBot'
import SingleChoiceList from './questionType/SingleChoiceList'
import SingleChoiceSelect from './questionType/SingleChoiceSelect'

export default {
  name: 'Conversation',
  components: {
    SystemBot,
    SingleChoiceList,
    SingleChoiceSelect
  },
  props: {
    conversation: {
      type: Object
    }
  },
  data () {
    return {
      answer: null
    }
  },
  mounted () {
    // scroll to question top
    this.scrollTo('question' + this.conversation.code)
  },
  watch: {
    answer (value) {
      window.setTimeout(() => {
        let answerObj = {}
        answerObj[this.conversation.code] = this.answer.code
        this.$store.commit('updateResponse', answerObj)
      }, 500)
    }
  }
}
</script>
<style lang="scss" scoped>
.single-conversation {
  margin-bottom: 32px;

  .user-tips{
    text-align: right;
    margin: 48px 0px 12px;
    color: $theme-color-gray;
  }

  .user-response {
    display: flex;
    justify-content: flex-end;
  }

  .selected-answer {
    max-width: 200px;
    background: linear-gradient(90deg, #0BB5F6 0%, #06DBC3 100%);
    color: $theme-color-white;
    padding: 8px;
    border-radius: 4px;
    margin-top: 32px;
  }
}

@media( min-width: 1023px ){
  .single-conversation {
    .selected-answer {
      max-width: 330px;
    }
  }
}
</style>



