<template>
  <div class="single-choice-list">
    <div class="single-option"
      v-for="option in optionList"
      :key="option.code"
      @click="chooseOption( option )"
    >
      {{ option.text }}
    </div>
  </div>
</template>
<script>
export default {
  name: 'SingleChoiceList',
  props: {
    value: {
      type: Object
    },
    optionList: {
      type: Array,
      default () {
        return []
      }
    }
  },
  methods: {
    chooseOption (option) {
      this.$emit('input', option)
    }
  }
}
</script>
<style lang="scss" scoped>
.single-choice-list {
  .single-option {
    background-color: #30394B;
    padding: 8px;
    color: #BEC6DB;
    border-radius: 4px;
    cursor: pointer;

    &:not(:last-child) {
      margin-bottom: 12px;
    }
  }
  .single-option:hover{
    background-color: $theme-chart-background-gray;
  }
}
</style>



