<template>
  <div class="single-choice-select-block">
    <select class="single-choice-select"
      :name="name"
      v-model="selectedOption"
    >
      <option value="null">请选择</option>
      <option
        v-for="option in optionList"
        :key="option.code"
        :value="option"
      >{{ option.text }}</option>
    </select>
    <div class="arrow"></div>
  </div>
</template>
<script>
export default {
  name: 'SingleChoiceSelect',
  props: {
    value: {
      type: Object,
      default () {
        return {}
      }
    },
    name: {
      type: String,
      default: ''
    },
    optionList: {
      type: Array,
      default () {
        return []
      }
    }
  },
  computed: {
    selectedOption: {
      get () {
        return this.value
      },
      set (option) {
        this.$emit('input', option)
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.single-choice-select-block {
  position: relative;
  border-bottom: 1px solid rgba(255, 255, 255, 0.54);

  .single-choice-select {
    width: 100%;
    border: none;
    background-color: transparent;
    padding: 0 0 6px 0;
  }

  .arrow {
    position: absolute;
    top: 0;
    right: 0;
    width: 16px;
    height: 100%;
    background-color: $theme-color-thin-black;

    &:after {
      display: block;
      content: '';
      position: absolute;
      top: 4px;
      width: 6px;
      height: 6px;
      border-width: 2px;
      border-style: solid;
      border-color: #FFFFFF #FFFFFF transparent transparent;
      transform: rotate(135deg);
    }
  }
}
</style>



