<template>
  <div class="page-question">
    <div class="progress-block">
      <div class="progress-bar">
        <div class="progress"
          :style="{ width: progressWidth * 100 + '%' }"
        ></div>
      </div>
    </div>
    <transition-group
      name="fade"
      mode="out-in"
    >
      <conversation
        v-if="index <= responseList.length"
        v-for="(conversation, index) in questionList"
        :conversation="conversation"
        :key="conversation.code"
      ></conversation>
    </transition-group>
    <div id="word_first"></div>
    <system-bot v-if="showFirstTip"
      :msg="tip.word_first"
      class="tip-word"
    ></system-bot>
    <div v-if="showSpinner" class="analyze-dot-box">
      <div class="analyze-spinner spinner_1"></div>
      <div class="analyze-spinner spinner_2"></div>
      <div class="analyze-spinner spinner_3"></div>
    </div>
    <div id="word_second"></div>
    <system-bot v-if="showSecondTip"
      :msg="tip.word_second"
    ></system-bot>
  </div>
</template>
<script>
import Conversation from './components/Conversation'
import SystemBot from './components/SystemBot'
import { sendCompanyData } from '@/API/analyze'

export default {
  name: 'PageQuestion',
  data () {
    return {
      contactData: {
        QUEST_REPORT: '',
        COMPANY_NO: '',
        CONTACT_NAME: '',
        CONTACT_TITLE: '',
        MOBILE_PHONE: '',
        COMPANY_NAME: '',
        COMPANY_PHONE: '',
        COMPANY_EMAIL: '',
        EMAIL_CHECK: '',
        CITY_CODE: '',
        CATEGORY_CODE: '',
        CONTACT_TIME: '',
        IMPORT_SYSTEM_TIME: '',
        BUDGET: '',
        REMARK: ''
      },
      showFirstTip: false,
      showSecondTip: false,
      showSpinner: false,
      tip: {
        word_first: '正在为您解析结果，请稍等一下',
        word_second: '分析结果已出来，页面将自动跳转'
      }
    }
  },
  components: {
    Conversation,
    SystemBot
  },
  watch: {
    'responseList.length' (value) {
      if (value === this.questionList.length) {
        this.showFirstTip = true
        this.showSpinner = true
        this.scrollTo('word_first')
        // 將問卷資料傳入 vuex
        let dt = new Date().getTime() + ''
        this.contactData.COMPANY_NO = dt.substr(3)
        this.contactData.QUEST_REPORT = this.responseList
        this.contactData.CITY_CODE = this.responseList[0].Q027
        this.contactData.CATEGORY_CODE = this.responseList[1].Q028
        this.$store.commit('addCompanyNum', this.contactData.COMPANY_NO)
        // sendCompanyData(
        //   this.contactData
        // )
        //   .then(response => {
            setTimeout(() => {
              this.scrollTo('word_second')
              this.showSpinner = false
              this.showSecondTip = true
              setTimeout(() => {
                this.$router.push({
                  name: 'ContactUs'
                })
              }, 3000)
            }, 2000)
          // })
          // .catch(err => {
          //   console.log(err)
          // })
      }
    }
  },
  methods: {
  },
  computed: {
    language () {
      return this.$store.state.language
    },
    progressWidth () {
      return this.responseList.length / this.questionList.length
    },
    questionList () {
      return this.$store.state.questionList
    },
    responseList () {
      return this.$store.state.responseList
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.page-question {
  padding: 100px 24px 70vh;

  .progress-block {
    position: fixed;
    top: 50px;
    left: 0px;
    width: 100%;
    padding: 19px 12px 22px;
    z-index: 999;
    background-color: $theme-color-black;

    .progress-bar {
      width: 100%;
      height: 9px;
      border-radius: 9px;
      background: rgba(255, 255, 255, 0.12);
      overflow: hidden;
    }
    .progress {
      height: 9px;
      border-radius: 9px;
      background-color: $theme-color-primary;
      transition: width 0.3s;
    }
  }

  .tip-word{
    margin-bottom: 32px;
  }

  .analyze-dot-box{
      width: 100%;
      display: flex;
      justify-content: center;
      position: fixed;
      bottom: 5%;
      left: 0px;

    .analyze-spinner{
      width: 14px;
      height: 14px;
      border-radius: 50%;
      background: $theme-color-primary;
      margin: 8px;
    }

    .spinner_1{
      animation:spinner1 1.5s  infinite;
    }
    @keyframes spinner1 {
      0%{
        transform:translate(0,0);
      }
      25%{
        transform:translate(0,-15px);
      }
      50%{
        transform:translate(0,0px);
      }
      100%{
        transform:translate(0,0);
      }

    }
    .spinner_2{
      animation:spinner2 1.5s 0.15s infinite;
    }
    @keyframes spinner2 {
      0%{
        transform:translate(0,0);
      }
      25%{
        transform:translate(0,-15px);
      }
      50%{
        transform:translate(0,0px);
      }
      100%{
        transform:translate(0,0);
      }
    }
    .spinner_3{
      animation:spinner3 1.5s 0.3s infinite;
    }
    @keyframes spinner3 {
      0%{
        transform:translate(0,0);
      }
      25%{
        transform:translate(0,-15px);
      }
      50%{
        transform:translate(0,0px);
      }
      100%{
        transform:translate(0,0);
      }
    }
  }
}

.question-conversation{
  margin-bottom: 60px;
}
</style>



