<template>
  <div class="system-bot">
    <div class="bot-img-block">
      <img class="bot-img" src="@/assets/chatbox_icon.png" alt="user-icon">
    </div>
    <div class="chat-box system">{{ msg }}</div>
  </div>
</template>
<script>
export default {
  name: 'SystemBot',
  props: {
    msg: {
      type: String,
      default: null
    }
  }
}
</script>
<style lang="scss" scoped>
.system-bot {
  display: flex;
  align-items: flex-start;

  .bot-img-block {
    width: 48px;
    height: 48px;
    margin-right: 8px;
  }
  .bot-img {
    width: 75%;
    height: auto;
    margin-top: -8px;
  }
}
</style>



